/*eslint-disable*/

import { combineReducers, createStore } from 'redux';

let goodsState = [
  { id : 0, name : '', quan : 2 },
  { id : 1, name : '', quan : 0 },
  { id : 2, name : 'redux  ', quan : 100 }
];

let alertState = true;

function reducer(state = goodsState, action){
  if ( action.type === '' ){
    let copyState = [...state];
    copyState.push(action.payload);
    return copyState
  }

  if ( action.type === '' ){
    let copyState = [...state];
    copyState[0].quan++;
    return copyState
  } else if( action.type === '' ){
    let copyState = [...state];
    if ( copyState[0].quan > 0 ){
      copyState[0].quan--;
    }
    return copyState
  } else {
    return state
  }
}

function reducer2(state = alertState, action){
  if ( action.type === '' ){
    state = false;
    return state
  } else {
    return state
  }
}

let store = createStore(combineReducers({reducer, reducer2}));

export default store